@import url(https://fonts.googleapis.com/css?family=Julius+Sans+One|Raleway|Source+Sans+Pro:300,400);
html{box-sizing:border-box;font-size:16px}*,*:before,*:after{box-sizing:inherit}body,h1,h2,h3,h4,h5,h6,p,ol,ul{margin:0;padding:0;font-weight:normal}ol,ul{list-style:none}img{max-width:100%;height:auto}html{font-size:14px}@media (min-width: 376px){html{font-size:16px}}@media (min-width: 426px){html{font-size:18px}}@media (min-width: 769px){html{font-size:20px}}@media (min-width: 1440px){html{font-size:22px}}body,h1,h2,h3,h4,h5,h6,p,ol,ul{font-family:'Roboto', sans-serif;font-weight:300}h1{font-weight:900}.content-grid{margin:0 auto;width:calc(100% - 5rem)}@media (min-width: 426px){.content-grid{width:calc(100% - 5rem)}}@media (min-width: 769px){.content-grid{width:calc(100% - 10rem)}}@media (min-width: 1025px){.content-grid{width:calc(100% - 20rem)}}::-webkit-scrollbar{display:none}a{text-decoration:none}

.social-icons{padding:0.25rem 0;font-size:3rem}@media (max-width: 425px){.social-icons{font-size:2.5rem}}.social-icons a{padding:0 0.625rem}.social-icons.animate-icons a{display:inline-block;-webkit-transform:perspective(1px) translateZ(0);transform:perspective(1px) translateZ(0);box-shadow:0 0 1px transparent}.social-icons.animate-icons a:hover,.social-icons.animate-icons a:focus,.social-icons.animate-icons a:active{-webkit-animation-name:icon-jump;animation-name:icon-jump;-webkit-animation-duration:0.45s;animation-duration:0.45s;-webkit-animation-timing-function:ease-in-out;animation-timing-function:ease-in-out;-webkit-animation-iteration-count:1;animation-iteration-count:1}@-webkit-keyframes icon-jump{50%{-webkit-transform:translateY(-6px);transform:translateY(-6px)}100%{-webkit-transform:translateY(0px);transform:translateY(0px)}}@keyframes icon-jump{50%{-webkit-transform:translateY(-6px);transform:translateY(-6px)}100%{-webkit-transform:translateY(0px);transform:translateY(0px)}}

.scroll-to-next{cursor:pointer}.scroll-to-next .arrow{text-align:center;font-size:38px;margin:0 auto;padding-bottom:0.5rem}.scroll-to-next .arrow .scroll-text{font-family:'Source Sans Pro', sans-serif;text-align:center;font-size:0.7rem;font-weight:300;margin-bottom:-0.125rem}.scroll-to-next .bounce{-webkit-animation:bounce 2s infinite;animation:bounce 2s infinite}@-webkit-keyframes bounce{0%,20%,50%,80%,100%{-webkit-transform:translateY(0);transform:translateY(0)}40%{-webkit-transform:translateY(-30px);transform:translateY(-30px)}60%{-webkit-transform:translateY(-15px);transform:translateY(-15px)}}@keyframes bounce{0%,20%,50%,80%,100%{-webkit-transform:translateY(0);transform:translateY(0)}40%{-webkit-transform:translateY(-30px);transform:translateY(-30px)}60%{-webkit-transform:translateY(-15px);transform:translateY(-15px)}}

.landing-page{min-height:100vh;display:flex;flex:0 1 auto;flex-direction:column;justify-content:center}.landing-page main{display:flex;flex:1 0 auto;flex-direction:column;justify-content:center;padding:3.25rem 1.5rem 1rem;text-align:center}.landing-page main .intro-wrapper{flex:auto;display:flex;flex-direction:column;justify-content:center;text-align:center}.landing-page main .intro-wrapper>.intro-name{font-family:'Julius Sans One', sans-serif;font-size:5rem;line-height:4rem;text-transform:uppercase;padding-bottom:1rem}@media (max-width: 425px){.landing-page main .intro-wrapper>.intro-name{font-size:3.5rem}}.landing-page main .intro-wrapper>.tagline{font-family:'Source Sans Pro', sans-serif;font-size:1.5rem;font-weight:300;letter-spacing:0.1rem;margin:1.5rem 0}@media (max-width: 425px){.landing-page main .intro-wrapper>.tagline{font-size:1rem}}.landing-page main .intro-wrapper>.icon{margin:1.5rem 0}

.about-page{padding-top:2rem;min-height:100vh;display:flex;flex:0 1 auto;flex-direction:column;justify-content:center}.about-page h1{font-family:'Julius Sans One', sans-serif;padding:1rem 0;font-size:3rem}@media (max-width: 425px){.about-page h1{font-size:2rem}}.about-page .content-grid{flex:auto}.about-page .about-wrapper .about-content{font-size:0.875rem;line-height:1.5rem}.about-page .about-wrapper .about-content p{font-family:'Source Sans Pro', sans-serif;font-weight:400;padding:0.5rem 0}.about-page .about-wrapper .about-content .text-emoji{padding:2rem 0;font-size:1.5rem;font-weight:300;text-align:center}

.portfolio-item{display:flex;flex-direction:column;margin:0.5rem 0;border-radius:2px;-webkit-transition:box-shadow 0.25s;transition:box-shadow 0.25s;box-shadow:0 1px 1px 0 rgba(0,0,0,0.14),0 1px 3px 0 rgba(0,0,0,0.12),0 2px 1px -2px rgba(0,0,0,0.2);width:12rem;margin:1rem 1rem}@media (max-width: 425px){.portfolio-item{width:15rem}}.portfolio-item:hover{box-shadow:0 6px 12px 0 rgba(0,0,0,0.2)}.portfolio-item__title{font-family:'Raleway', sans-serif;flex:1 1;padding:1rem 1.5rem;font-size:1.1rem;text-align:center}@media (max-width: 425px){.portfolio-item__title{font-size:1.25rem}}.portfolio-item__desc{font-family:'Source Sans Pro', sans-serif;display:flex;flex-direction:column;padding:0 0.5rem;font-size:0.85rem;font-weight:400;text-align:center;line-height:1.25rem}@media (max-width: 425px){.portfolio-item__desc{font-size:0.875rem}}.portfolio-item__icon{padding:1rem 0;font-size:1.5rem;font-weight:100;text-align:center}.portfolio-item__icon svg{margin:0 0.25rem}.portfolio-item__links{font-family:'Source Sans Pro', sans-serif;padding:0 0 0.75rem;display:flex;justify-content:space-around;font-size:0.875rem;font-weight:400}.portfolio-item__links a{padding-bottom:0.25rem;border-bottom:2px solid transparent}.portfolio-item__links a:hover{cursor:pointer;border-bottom:2px solid inherit}

.scroll-to-top{cursor:pointer}.scroll-to-top .arrow{text-align:center;margin:0 auto;font-size:38px;padding-bottom:0.5rem}.scroll-to-top .arrow .scroll-text{font-family:'Source Sans Pro', sans-serif;text-align:center;font-size:0.7rem;margin-bottom:-0.125rem}.scroll-to-top .bounce{-webkit-animation:bounce 2s infinite;animation:bounce 2s infinite}@-webkit-keyframes bounce{0%,20%,50%,80%,100%{-webkit-transform:translateY(0);transform:translateY(0)}40%{-webkit-transform:translateY(-30px);transform:translateY(-30px)}60%{-webkit-transform:translateY(-15px);transform:translateY(-15px)}}@keyframes bounce{0%,20%,50%,80%,100%{-webkit-transform:translateY(0);transform:translateY(0)}40%{-webkit-transform:translateY(-30px);transform:translateY(-30px)}60%{-webkit-transform:translateY(-15px);transform:translateY(-15px)}}

.projects-page{padding-top:2rem;min-height:100vh;display:flex;flex:0 1 auto;flex-direction:column;align-items:center}@media (max-width: 425px){.projects-page{padding-top:3rem}}.projects-page h1{font-family:'Julius Sans One', sans-serif;padding:1rem 0;font-size:3rem}@media (max-width: 425px){.projects-page h1{font-size:2rem}}.projects-page .content-grid{flex:auto}.projects-page .projects-wrapper{display:flex;flex-wrap:wrap;align-items:center;justify-content:center}@media (max-width: 425px){.projects-page .projects-wrapper{flex-direction:column;align-items:center}}

nav{position:fixed;top:0;width:100%;height:2.5rem;display:flex;align-items:center;justify-content:flex-end}@media (max-width: 425px){nav{height:3.5rem}}nav .theme-icon{cursor:pointer;justify-self:flex-start;margin-left:1rem;display:flex;flex-direction:column;align-items:center;padding:0.25rem 0;font-size:2rem}@media (max-width: 425px){nav .theme-icon{font-size:2.2rem}}nav .theme-icon.bounce-xy{-webkit-animation:bounce-xy 2s infinite;animation:bounce-xy 2s infinite}@-webkit-keyframes bounce-xy{0%,20%,50%,80%,100%{-webkit-transform:translateY(0) translateX(0);transform:translateY(0) translateX(0)}40%{-webkit-transform:translateY(2px) translateX(2px);transform:translateY(2px) translateX(2px)}60%{-webkit-transform:translateY(1px) translateX(1px);transform:translateY(1px) translateX(1px)}}@keyframes bounce-xy{0%,20%,50%,80%,100%{-webkit-transform:translateY(0) translateX(0);transform:translateY(0) translateX(0)}40%{-webkit-transform:translateY(2px) translateX(2px);transform:translateY(2px) translateX(2px)}60%{-webkit-transform:translateY(1px) translateX(1px);transform:translateY(1px) translateX(1px)}}nav .menu{margin-right:1rem;display:flex;flex:1 1;justify-content:flex-end}nav .menu__item{font-family:'Raleway', sans-serif;padding:0.25rem 0;margin:0 1rem;font-size:1.25rem;font-weight:400;border-bottom:2px solid transparent}nav .menu__item:hover{cursor:pointer}

