@import 'breakpoints';
@import 'reset';
@import url('https://fonts.googleapis.com/css?family=Julius+Sans+One|Raleway|Source+Sans+Pro:300,400');

/* Typography */

/* roboto - latin */

html {
  font-size: 14px;

  @include breakpoint-small-up {
    font-size: 16px;
  }

  @include breakpoint-medium-up {
    font-size: 18px;
  }

  @include breakpoint-large-up {
    font-size: 20px;
  }

  @include breakpoint-xxlarge-up {
    font-size: 22px;
  }
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul {
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
}

h1 {
  font-weight: 900;
}

.content-grid {
  margin: 0 auto;
  width: calc(100% - 5rem); // mobile

  @include breakpoint-medium-up {
    width: calc(100% - 5rem);
  }

  @include breakpoint-large-up {
    width: calc(100% - 10rem);
  }

  @include breakpoint-xlarge-up {
    width: calc(100% - 20rem);
  }
}

::-webkit-scrollbar {
  display: none;
}

a {
  text-decoration: none;
}
